.header {
  position: absolute;
  left: 60px;
  right: 60px;
  top: 40px; }

.header__logo-name {
  color: transparent;
  user-select: none;
  cursor: default; }

.header__logo-svg {
  float: left; }

.header__row {
  list-style: none;
  text-align: right; }

.header__list {
  display: inline-block;
  margin-left: 60px; }
  .header__list a {
    font-size: 160%;
    color: #9F9F9F;
    letter-spacing: 0.2em;
    text-decoration: none;
    transition: all 0.25s ease-in-out 0.001s; }
    .header__list a:hover {
      color: #F4F4F4; }
    .header__list a:hover > .header__list--underline {
      width: 100%;
      background-color: #F4F4F4; }

.header__list--underline {
  background-color: #7A7A7A;
  margin-top: 0.1em;
  height: 1px;
  width: 0;
  transition: all 0.25s ease-in-out; }

.main {
  padding-top: 200px; }

.main-slogan__wrapper {
  cursor: default;
  user-select: none;
  padding-left: 120px;
  max-width: 600px;
  font-size: 2.4rem;
  letter-spacing: 0.18em; }

.main-hero__left-hand {
  float: left;
  max-width: 43%; }
  .main-hero__left-hand img {
    width: 100%;
    height: 100%; }

.main-hero__right-hand {
  float: right;
  max-width: 57%; }
  .main-hero__right-hand img {
    width: 100%;
    height: 100%; }

html, body {
  background-color: #151515;
  box-sizing: border-box;
  color: #F4F4F4;
  font-size: 15px;
  font-family: "Amatic SC", serif;
  font-weight: 100; }

*, *:before, *:after {
  box-sizing: inherit; }

@media screen and (max-width: 720px) {
  .header__nav-container {
    display: none; }
  .header {
    text-align: center;
    top: 20px; }
  .header__logo-svg {
    float: none; }
    .header__logo-svg svg {
      height: auto;
      width: 180px; }
  .main {
    padding-top: 160px; }
  .main-slogan__wrapper {
    text-align: center;
    padding: 0 20px;
    margin: 0 auto;
    font-size: 1.4rem; } }
